import chunLeiModal from '@/components/chunLei-modal/chunLei-modal.vue'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import LoginPage from '@/components/login-page.vue';
export default {
	data() {
		return {
			showReadcodeInput: false,
			mData: {
				title: '输入阅读码',
				content: [{
					content: '',
					type: 'text',
					placeholder: '请输入阅读码'
				}, ]
			},
			descData: {
				title: '购买说明',
				content: '1. 购买成功后可直接点击查看阅读期刊内容， 或在“ 我的杂志” 列表查看；\r\n2. 电子刊为虚拟商品， 如无系统问题， 购买后不可退换；\r\n3. 每次购买成功后， 阅读码将显示在阅读码列表中；\r\n4. 一个阅读码只可以被激活一次， 未激活的阅读码可赠与好友；\r\n5. 如有其他使用问题可私信官方账号获取支持；',
				cancelText: 'cancel'
			},
			buyTypes: [],
			ordinaryNum: 0,
			choosenIndex: 0,
			showDescModal: false,
			isIos: false,
			magId: "",
			isBuy: false,
			perviewCurrent: 1,
			perviewImgs: [],
			magTitle: "",
			showLoginPage: false,
			isUnload: false
		};
	},
	onUnload() {
		this.isUnload = true;
	},
	onLoad(option) {
		this.isUnload = false;
		this.magId = option.magId;
		this.getMagInfo();
		let that = this;
		uni.getSystemInfo({
			success: function(res) {
				if (res.platform == 'ios') {
					that.isIos = true;
				}
			}
		})
	},
	methods: {
		async getMagInfo() {
			let res;
			if (this.$store.state.token) {
				res = await this.myRequest('/api/magazine/preview', {
					magazine_id: this.magId
				}, 'POST');
			} else {
				res = await this.myRequest('/api/magazine/preview', {
					magazine_id: this.magId
				}, 'POST', false);
			}
			if (res.error_code == 0) {
				this.isBuy = res.data.is_buy;
				this.perviewImgs = res.data.preview_join;
				this.magTitle = res.data.title;
				if (!this.isUnload) {
					uni.setNavigationBarTitle({
						title: res.data.title
					});
				}
			}
		},
		async clickBuy() {
			if (this.$store.state.token) {
				let res = await this.myRequest('/common/getSpecs', {}, 'GET', false);
				if (res.error_code == 0) {
					this.buyTypes = res.data;
					this.$refs.buyCode.open()
				}
			} else {
				this.showLoginPage = true;
			}
		},
		onSwiperChange(e) {
			this.perviewCurrent = e.detail.current + 1
		},
		async onReadcodeInputConfirm(content) {
			if (content[0].content) {
				let res = await this.myRequest('/api/magazine/active', {
					magazine_id: this.magId,
					code: content[0].content
				}, 'POST');
				if (res.error_code == 0) {
					this.$store.commit('saveNeedFresh', true);
					this.goRead();
				}
			}
		},
		clickType(index) {
			this.choosenIndex = index;
		},
		clickOrdinary(flag) {
			if (this.choosenIndex !== 4) {
				this.choosenIndex = 4;
				return;
			}
			if (flag) {
				this.ordinaryNum++;
			} else {
				if (this.ordinaryNum == 0) return;
				this.ordinaryNum--;
			}
		},
		showDesc() {
			this.showDescModal = true;
		},
		async goPay() {
			let num = this.choosenIndex == 4 ? this.ordinaryNum : this.buyTypes[this.choosenIndex].num;
			num = parseInt(num);
			let res = await this.myRequest('/api/order/appletPay', {
				magazine_id: this.magId,
				buy_num: num
			}, 'POST');
			if (res.error_code == 0) {
				uni.requestPayment({
					timeStamp: res.data.timestamp,
					nonceStr: res.data.nonceStr,
					package: res.data.package,
					signType: 'MD5',
					paySign: res.data.paySign,
					success: (res) => {
						uni.showToast({
							title: "购买成功"
						})
						this.$store.commit('saveNeedFresh', true);
						setTimeout(() => {
							uni.redirectTo({
								url: `/pages/magazinefirst/magazinefirst?magId=${this.magId}`
							})
						}, 1500)
					},
					fail: (res) => {
						console.log("支付失败,原因为: " + res
							.errMsg)
					},
					complete: () => {}
				})
			}
		},
		useReadCode() {
			console.log('usecodetoken', this.$store.state.token)
			if (this.$store.state.token) {
				this.showReadcodeInput = true;
			} else {
				this.showLoginPage = true;
			}
		},
		goRead() {
			uni.navigateTo({
				url: `/pages/read/read?magId=${this.magId}`
			});
		},
		loginOver() {
			this.showLoginPage = false;
			this.getMagInfo()
		}
	},
	computed: {
		price: function() {
			if (this.choosenIndex == 4) {
				let single = 0;
				if (this.ordinaryNum < 10) {
					single = 8;
				} else if (this.ordinaryNum >= 10 && this.ordinaryNum <= 50) {
					single = 7;
				} else if (this.ordinaryNum > 50) {
					single = 6;
				}
				return this.ordinaryNum * single;
			} else {
				return this.buyTypes[this.choosenIndex] && this.buyTypes[this.choosenIndex].price;
			}
		}
	},
	components: {
		chunLeiModal,
		uniPopup,
		LoginPage
	}
}
